:root
{
  // --PrimaryColor: hsl(26, 93%, 50%);
  --PrimaryColor: #3aafa9;
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 12%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greyBg: hsl(0, 0%, 77%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
}

.contactContainer
{
  .container
  {
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding-top: -200px;
    background-color: #fafafa;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;

    .form 
    {
      width: 100%;
      max-width: 820px;
      background-color: #fff;
      border-radius: 10px;
      box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);
      overflow: hidden;
      display: grid;
      grid-template-columns: repeat(2, 1fr);


      .contact-form 
      {
        background-color: #1abc9c;
        position: relative;
      }

      .contact-form:before 
      {
        content: "";
        position: absolute;
        width: 26px;
        height: 26px;
        background-color: #1abc9c;
        transform: rotate(45deg);
        top: 50px;
        left: -13px;
      }

      .title {
        color: #fff;
        font-weight: 500;
        font-size: 1.5rem;
        line-height: 1;
        margin-bottom: 0.7rem;
      }

      .input-container {
        position: relative;
        margin: 1rem 0;
      }
      
      .input {
        width: 100%;
        outline: none;
        border: 2px solid #fafafa;
        background: none;
        padding: 0.6rem 1.2rem;
        color: #fff;
        font-weight: 500;
        font-size: 0.95rem;
        letter-spacing: 0.5px;
        border-radius: 25px;
        transition: 0.3s;
      }

      textarea.input {
        padding: 0.8rem 1.2rem;
        min-height: 150px;
        border-radius: 22px;
        resize: none;
        overflow-y: auto;
      }
      
      .input-container label {
        position: absolute;
        top: 50%;
        left: 15px;
        transform: translateY(-50%);
        padding: 0 0.4rem;
        color: #fafafa;
        font-size: 0.9rem;
        font-weight: 400;
        pointer-events: none;
        z-index: 1000;
        transition: 0.5s;
      }
      
      .input-container.textarea label {
        top: 1rem;
        transform: translateY(0);
      }

      .btn {
        padding: 0.6rem 1.3rem;
        background-color: #fff;
        border: 2px solid #fafafa;
        font-size: 0.95rem;
        color: #1abc9c;
        line-height: 1;
        border-radius: 25px;
        outline: none;
        cursor: pointer;
        transition: 0.3s;
        margin: 0;
      }

      .btn:hover 
      {
        background-color: transparent;
        color: #fff;
      }

      .input-container span 
      {
        position: absolute;
        top: 0;
        left: 25px;
        transform: translateY(-50%);
        font-size: 0.8rem;
        padding: 0 0.4rem;
        color: transparent;
        pointer-events: none;
        z-index: 500;
      }


      .input-container span:before,
      .input-container span:after {
        content: "";
        position: absolute;
        width: 10%;
        opacity: 0;
        transition: 0.3s;
        height: 5px;
        background-color: #1abc9c;
        top: 50%;
        transform: translateY(-50%);
      }

      .input-container span:before {
        left: 50%;
      }
      
      .input-container span:after {
        right: 50%;
      }
      
      .input-container.focus label {
        top: 0;
        transform: translateY(-50%);
        left: 25px;
        font-size: 0.8rem;
      }
      
      .input-container.focus span:before,
      .input-container.focus span:after {
        width: 50%;
        opacity: 1;
      } 
      
      

      .contact-info {
        padding: 2.3rem 2.2rem;
        position: relative;
        b

        .title {
          color: #1abc9c;
        }

        .text {
          color: #333;
          margin: 1.5rem 0 2rem 0;
        }

        .information {
          display: flex;
          color: #555;
          margin: 0.7rem 0;
          align-items: center;
          font-size: 0.95rem;
        }
        
        .icon {
          width: 32px;
          padding: 3px 0 0 3px;
          margin-right: 0.7rem;
        }

        .social-media {
          padding: 2rem 0 0 0;
        }
        
        .social-media p {
          color: #333;
        }
        
        .social-icons {
          display: flex;
          margin-top: 0.5rem;
        }
        
        .social-icons a {
          width: 35px;
          height: 35px;
          border-radius: 5px;
          background: linear-gradient(45deg, #1abc9c, #149279);
          color: #fff;
          text-align: center;
          line-height: 35px;
          margin-right: 0.5rem;
          transition: 0.3s;
        }
        
        .social-icons a:hover {
          transform: scale(1.05);
        }
        
        .contact-info:before {
          content: "";
          position: absolute;
          width: 110px;
          height: 100px;
          border: 22px solid #1abc9c;
          border-radius: 50%;
          bottom: -77px;
          right: 50px;
          opacity: 0.3;
        }
      }
    }

    form {
      padding: 2.3rem 2.2rem;
      z-index: 10;
      overflow: hidden;
      position: relative;
    }
  }
}


// Media Queries----------
@media screen and (max-width: 850px) 
{

  .contactContainer
  {
    .container
    {

      .form 
      {
        grid-template-columns: 1fr;
      }
    
      .contact-info:before 
      {
        bottom: initial;
        top: -75px;
        right: 65px;
        transform: scale(0.95);
      }
    
      .contact-form:before 
      {
        top: -13px;
        left: initial;
        right: 70px;
      }
    
      
    
      .text 
      {
        margin: 1rem 0 1.5rem 0;
      }
    
      .social-media 
      {
        padding: 1.5rem 0 0 0;
      }
      }
    }
}

@media screen and (max-width: 480px) 
{

  .contactContainer
  {
    
    .container 
    {
      padding: 1.5rem;

      .contact-info:before 
      {
        display: none;
      }
    
    
      form,
      .contact-info 
      {
        padding: 1.7rem 1.6rem;
      }
    
      .text,
      .information,
      .social-media p 
      {
        font-size: 0.8rem;
      }
    
      .title 
      {
        font-size: 1.15rem;
      }
    
      .social-icons a 
      {
        width: 30px;
        height: 30px;
        line-height: 30px;
      }
    
      .icon 
      {
        width: 23px;
      }
    
      .input 
      {
        padding: 0.45rem 1.2rem;
      }
    
      .btn 
      {
        padding: 0.45rem 1.2rem;
      }
    }
  
  }

}