:root {
  --PrimaryColor: #3aafa9;
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 12%);
  --textColor: hsl(240, 1%, 48%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greyBg: hsl(0, 0%, 77%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
}

.locationSection .locationContainer {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 80%;
  max-width: 1880px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  background-color: #fff;
}
.locationSection .locationContainer .locationInfo {
  flex: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #f5f5f5;
}
.locationSection .locationContainer .locationInfo h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: var(--PrimaryColor);
}
.locationSection .locationContainer .locationInfo p {
  margin-bottom: 40px;
  font-size: 1.2em;
}
.locationSection .locationContainer .locationInfo .information {
  display: flex;
  gap: 40px;
}
.locationSection .locationContainer .locationInfo .information .info {
  font-size: 1.5em;
}
.locationSection .locationContainer .locationInfo .information .info h2 {
  font-size: 3em;
  margin: 0;
}
.locationSection .locationContainer .locationInfo .information .info span {
  font-size: 0.5em;
  vertical-align: super;
  color: var(--blackColor);
}
.locationSection .locationContainer .map {
  flex: 1;
  min-width: 300px;
}
.locationSection .locationContainer .map iframe {
  border: 2px solid #3aafa9;
  border-radius: 1%;
  width: 100%;
  height: 500px;
}

@media (max-width: 768px) {
  .locationSection .locationContainer .locationInfo .map {
    flex: 1 1 100%;
  }
  .locationSection .locationContainer .locationInfo {
    padding: 20px;
  }
}/*# sourceMappingURL=location.css.map */